@use "../../../sass/abstracts/sizing" as *;
@use "../../../sass/base/base" as *;
@use "../../../sass/components/custom-datepicker" as *;
@use "../../../sass/components/custom-dropdown" as *;
@use "../../../sass/components/custom-fields" as *;
@use "../../../sass/components/custom-links" as *;
@use "../../../sass/components/custom-radio" as *;
@use "../../../sass/components/custom-checkbox" as *;
@use "../../../sass/components/custom-buttons" as *;



.form-container {
    background-color: #fff;
    // padding: 32px 40px;
    border: 1px solid #A09F9F;
    border-radius: 4px;
    label{
        font-weight: 600;
    }
    .title-insurance{
        label{
            font-weight: 400;
        }
    }
    .language-selector{
        label{
            font-weight: 400;
        }
    }
}

.questionnaire__list {
    li {
        padding-bottom: 24px;
        border-bottom: 1px solid #D9D9D9;
    }
    .dropdownlist{
        padding-bottom: 8px;
    }
    label{
        font-weight: normal;
    }
}

.question__item {
    border-bottom: 1px solid #D9D9D9;
    label{
        font-weight: normal;
    }
    .bold{
        font-weight: 600;
    }
}

.submit-button {
    background-color: #FFCD34!important;
}

.new-client{
    display: none;
    &.active{
        display: flex;
    }
}

.new-client-hide{
    display: block;
}

.tooltip-registration {
    display: none;
}
